@import "./default/default.scss";
.current-page-info {
  position: absolute;
  padding-top: 34px;
  top: 0;
  left: 0;
  width: 100%;
  @media (max-width: $mobile) {
    padding-top: 10px;
  }
  &__container {
    display: flex;
    gap: 11px;
    .current-page-info__page-name {
      padding-right: 22px;
      position: relative;
      @include rale-500-14;
      color: $white;
      z-index: 2;
      &:not(:last-child)::after {
        pointer-events: none;
        content: "\e919";
        font-family: icomoon;
        font-size: 10px;
        position: absolute;
        right: 0;
      }
    }
  }
}
