@import "./../../info-page.scss";
@import "./../../checkbox.scss";

.registration-section {
  margin-top: 90px;
  &__container {
    .registration-section__title {
      font-family: Raleway;
      font-size: 32px;
      font-weight: 800;
      letter-spacing: 0.15px;
      @media (max-width: 750px) {
        text-align: center;
      }
    }
    .form-registration-section {
      margin-top: 14px;
      .section-form-registration {
        display: flex;
        justify-content: space-between;
        gap: 30px;
        max-width: 1214px;
        @media (max-width: 750px) {
          flex-direction: column;
          align-items: center;
        }
      }
      .block-inputs-section-form-registration {
        max-width: 474px;
        width: 100%;
        .sub-block-inputs-section-form-registration {
          display: flex;
          flex-direction: column;
          &__title {
            & + input {
              margin-top: 20px;
            }
          }
        }
        .tabs-block {
          margin-top: 37px;
          margin-bottom: 42px;
          .tab-bar {
            display: flex;
            justify-content: space-between;
          }
          hr {
            margin: 0;
            margin-top: -2px;
          }
          .tab-bar-item.tab-button {
            flex: auto;
            border: none;
            background: none;
            border-bottom: 3px solid rgba($color: #000000, $alpha: 0);
            font-family: Montserrat;
            font-size: 15px;
            font-weight: 500;
            padding-bottom: 10px;
            &:hover {
            }
            &:active {
            }
            &:focus {
            }
          }
          .tab-bar-item.tab-button.active {
            border-color: $darkGreen;
            font-weight: 700;
            &:hover {
            }
            &:active {
            }
            &:focus {
            }
          }
        }
      }

      .label-image {
        border: 1px solid rgba($color: #000000, $alpha: 0.08);
        margin-top: 63px;
        width: 131px;
        height: 170px;
        display: inline-block;
        position: relative;
        cursor: pointer;
        #uploadImage {
          width: 131px;
          height: 170px;
          object-fit: cover;
        }
        &:hover {
          &::before {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            content: "";
            background-color: rgba($color: $darkGreen, $alpha: 0.6);
          }
          &::after {
            position: absolute;
            top: 45%;
            display: block;
            width: fit-content;
            left: 0;
            right: 0;
            margin-left: auto;
            margin-right: auto;
            height: fit-content;
            text-align: center;
            content: "Загрузить фото";
            border-bottom: 1px dashed white;
            box-sizing: border-box;

            font-family: Montserrat;
            font-size: 13px;
            font-weight: 600px;
            color: #fff;
          }
        }
      }
      .sub-block-inputs-section-form-registration {
        &:not(:first-child) {
          margin-top: 51px;
        }
        &__title {
          font-family: Montserrat;
          font-weight: 700;
          font-size: 17px;
        }
        input {
          padding: 12px 20px 8px 15px;
          border: 0.5px solid rgba($color: #000000, $alpha: 0.15);
        }
        input + input {
          margin-top: 16px;
        }
        &.address-block {
          .sub-block-inputs-section-form-registration__title {
            margin-bottom: 5px;
          }
        }
        &.password-block {
          .sub-block-inputs-section-form-registration__title {
            margin-bottom: 3px;
          }
        }
      }
    }
  }
}
