@charset "UTF-8";
.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.current-page-info {
  position: absolute;
  padding-top: 34px;
  top: 0;
  left: 0;
  width: 100%;
}
@media (max-width: 600px) {
  .current-page-info {
    padding-top: 10px;
  }
}
.current-page-info__container {
  display: flex;
  gap: 11px;
}
.current-page-info__container .current-page-info__page-name {
  padding-right: 22px;
  position: relative;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.01em;
  color: #ffffff;
  z-index: 2;
}
.current-page-info__container .current-page-info__page-name:not(:last-child)::after {
  pointer-events: none;
  content: "\e919";
  font-family: icomoon;
  font-size: 10px;
  position: absolute;
  right: 0;
}

.checkbox-block .control {
  font-family: arial;
  display: block;
  position: relative;
  padding-left: 30px;
  margin-bottom: 5px;
  padding-top: 3px;
  cursor: pointer;
  font-size: 16px;
  color: 8A8A8A;
}
.checkbox-block .control input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.checkbox-block .control_indicator {
  position: absolute;
  top: 7px;
  left: 0;
  height: 14px;
  width: 14px;
  background: #fff;
  border: 2px solid #337d5a;
  border-radius: 0px;
}
.checkbox-block .control:hover input ~ .control_indicator,
.checkbox-block .control input:focus ~ .control_indicator {
  background: #fff;
}
.checkbox-block .control input:checked ~ .control_indicator {
  background: white;
}
.checkbox-block .control:hover input:not([disabled]):checked ~ .control_indicator,
.checkbox-block .control input:checked:focus ~ .control_indicator {
  background: white;
}
.checkbox-block .control input:disabled ~ .control_indicator {
  background: #e6e6e6;
  opacity: 0.6;
  pointer-events: none;
}
.checkbox-block .control_indicator:after {
  box-sizing: unset;
  content: "";
  position: absolute;
  display: none;
}
.checkbox-block .control input:checked ~ .control_indicator:after {
  display: block;
}
.checkbox-block .control-checkbox .control_indicator:after {
  left: 3px;
  top: 0px;
  width: 3px;
  height: 8px;
  border: solid #337d5a;
  border-width: 0 1px 1px 0;
  transform: rotate(45deg);
}
.checkbox-block .control-checkbox input:disabled ~ .control_indicator:after {
  border-color: #7b7b7b;
}

