  margin-right: auto;
  height: fit-content;
  text-align: center;
  content: "Загрузить фото";
  border-bottom: 1px dashed white;
  box-sizing: border-box;
  font-family: Montserrat;
  font-size: 13px;
  font-weight: 600px;
  color: #fff;
}
.registration-section__container .form-registration-section .sub-block-inputs-section-form-registration:not(:first-child) {
